import React, {Component} from "react";
import "react-vertical-timeline-component/style.min.css";
import Badge from "react-bootstrap/Badge";

class CV extends Component {
  render() {
    let sectionName = '';
    let cvItems = [];
    let downloadButtonText = '';

    if (this.props.resumeCV && this.props.resumeBasicInfo) {
      sectionName = this.props.resumeBasicInfo.section_name.cv;
      cvItems = this.props.resumeCV.map(function (cv, i) {
        return (
            <div key={i} className="row item">
              <div className="twelve columns">
                <h3>{cv.companyName}</h3>
                <p className="info">
                  {cv.specialization}
                  <span>&bull;</span>{" "}
                  <em className="date">{cv.MonthOfLeaving} {cv.YearOfLeaving}</em>
                </p>
                <p>{cv.Achievements}</p>
              </div>
            </div>
        );
      });

      // Asigna el texto del botón de descarga desde el JSON
      downloadButtonText = this.props.resumeBasicInfo.downloadCVText;
    }

    // Agrega el botón para descargar la hoja de vida
    const downloadButton = (
        <a href="/ruta/a/tu/hoja/de/vida.pdf" download="nombre_del_archivo.pdf" className="button">
          {downloadButtonText}
        </a>
    );

    return (
        <section id="cv">
          <div className="row education">
            <div className="three columns header-col">
              <h1><span>{sectionName}</span></h1>
            </div>
            <div className="nine columns main-col">
              {cvItems}
            </div>
          </div>
          <div className="row">
            <div className="twelve columns">
              {downloadButton}
            </div>
          </div>
        </section>
    );
  }
}

export default CV;
